.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
  /* margin-top: 20px; */
  /* margin-bottom: 20px; */
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}
.i-title-contribution {
  padding: 4px;
  height: 10%;
  animation: mov 5s ease-in-out infinite alternate;
}

.contri-link{
  text-decoration: none;
  color: darkkhaki;
}
.contri-link:hover{
  color: green;
}
@keyframes mov {
  15% {
    transform: translateX(-40px);
  }
  100%{
    transform:translateX(-50px);
  }
 
}

.i-title-item {
  /* margin-bottom: -30px; */
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #59b256;
  display: flex;
  align-items: center;
 
  
  
}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-img {
  padding: 3vh;
  margin-top: 4vh;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}


