.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 20px;
  background-color: rgb(237, 206, 52);
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: rgb(35, 34, 34);
}
.p-circle-1{
  background-color: aqua;
}
.p-circle-2{
  background-color: rgb(44, 152, 152);
}
.p-circle-3{
  background-color: rgb(97, 64, 227);
}

.p-img {
  width: 100%;
  transition: all 10s ease;
}

.p:hover .p-img {
  transform: translateY(-100%);
}

@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 20vh;
  }
}

